@font-face {
  font-family: 'GraphikLCG-light';

  src: local('Graphik LCG Light'), local('GraphikLCG-Light'),
    url('./fonts/GraphikLCG-Light.ttf') format('truetype');
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'GraphikLCG-semibold';

  src: local('Graphik LCG Semibold'), local('GraphikLCG-Semibold'),
    url('./fonts/GraphikLCG-Semibold.ttf') format('truetype');
  font-weight: 600;
  font-style: normal;
}

:root {
  --color-black: rgba(25, 28, 31, 1);
  --color-dark-gray: rgba(94, 100, 106, 1);
  --color-gray: rgba(131, 137, 143, 1);
  --color-mid-gray: rgba(194, 200, 205, 1);
  --color-light-gray: rgba(236, 239, 242, 1);
  --color-extralight-gray: rgba(246, 248, 250, 1);
  --color-white: rgba(255, 255, 255, 1);
  --color-dangerous: rgba(241, 54, 54, 1);
  --color-green-success: rgba(37, 205, 130, 1);
  --color-primary-disabled: rgba(171, 202, 230, 1);
  --color-primary-hover: rgba(57, 125, 255, 1);
  --color-primary: rgba(42, 108, 234, 1);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;

  font-family: 'GraphikLCG-semibold';
  line-height: 2.4rem;
  color: var(--color-white);

  background-color: rgba(255, 255, 255, 1);
  overflow-x: hidden;
}

.section {
  margin: 0 auto;
  max-width: 122rem;
  color: var(--color-black);
}

/* HEADER */

.header {
  position: relative;
  margin-bottom: 8rem;
  width: 100vw;
  height: 100vh;
  background-image: url(./img/Lunch_atop_a_Skyscraper\ 1.jpg);
}

.header__overlay {
  position: absolute;
  width: 100vw;
  height: 100vh;
  background-color: var(--color-primary);
  opacity: 80%;
}

/* NAVIGATION */

.nav {
  margin: 0 auto;
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 122rem;
  padding: 2.4rem;
  z-index: 100;
}

.nav__logo {
  width: 12rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  display: flex;
}

.circle {
  width: 2.4rem;
  height: 2.4rem;
  border-radius: 50%;
}

.blueCircle {
  position: relative;
  z-index: 5;
  background-color: var(--color-primary);
}

.whiteCircle {
  position: relative;
  z-index: 4;
  left: -1.2rem;
  background-color: var(--color-white);
}

.logo__text {
  font-size: 2rem;
}

.nav__links {
  display: flex;
  align-items: center;
  list-style: none;
}

.nav__item {
  margin-left: 2.4rem;
}

.nav__link {
  font-family: 'GraphikLCG-light';
  font-size: 1.6rem;
  color: var(--color-white);
  text-decoration: none;
}

.nav__link:hover {
  text-decoration: underline;
}

/* GREETING */

.header__greeting {
  position: absolute;
  z-index: 50;
  max-width: 74.8rem;
  left: calc(50vw - 37.4rem);
  top: calc(50vh - 16.7rem);

  display: flex;
  flex-direction: column;
  align-items: center;
}

.greeting__header {
  text-align: center;
  font-size: 5.4rem;
  line-height: 6.4rem;
  margin-bottom: 1.8rem;
}

.greeting__text {
  text-align: center;
  font-family: 'GraphikLCG-light';
  font-size: 1.8rem;
  line-height: 3.2rem;
  margin-bottom: 9.2rem;
}

.greeting__btn {
  padding: 2rem 4rem;
  border-radius: 0.5rem;
  border-width: 0;
  background-color: var(--color-light-gray);
  font-family: 'GraphikLCG-semibold';
  color: var(--color-black);
  font-size: 2rem;
}

/* SECTION_1 */

.firstSection {
  margin-bottom: 4.8rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 6.1rem;
}

.firstSection__title {
  font-size: 4rem;
}

.firstSection__cards {
  display: flex;
  flex-wrap: wrap;
  column-gap: 3.2rem;
}

.card {
  width: 28rem;
  display: flex;
  flex-direction: column;
  gap: 1.6rem;
  margin-bottom: 3.2rem;
}

.card__img {
  width: 7.2rem;
  height: 7.2rem;
}

.card__desc {
  display: flex;
  flex-direction: column;
  gap: 0.4rem;
}

.card__title {
  font-size: 1.6rem;
}

.card__text {
  font-family: 'GraphikLCG-light';
  font-size: 1.4rem;
}

/* SECTION_2 */

.secondSection {
  display: flex;
  align-items: center;
  column-gap: 3.2rem;
}

.secondSection__desc {
  display: flex;
  flex-direction: column;
  gap: 1.8rem;

  max-width: 59rem;
}

.secondSection__title {
  font-size: 3.2rem;
  line-height: 4rem;
}

.secondSection__text {
  font-family: 'GraphikLCG-light';
  font-size: 1.6rem;
  line-height: 2.8rem;
}

.secondSection__images {
  position: relative;
  max-width: 59rem;
}

.secondSection__back_img {
  position: absolute;
  z-index: -100;
}

.secondSection__img {
  width: 100%;
}

/* SECTION-3 */
.container {
  width: 100vw;
  background-color: var(--color-extralight-gray);
  padding: 5rem;
  margin-top: 8rem;
}

.thirdSection {
  display: flex;
  flex-direction: column;
  gap: 3.2rem;
  position: relative;
  z-index: 1;
}

.thirdSection__title {
  text-align: center;
  font-size: 4rem;
  line-height: 5.6rem;
}

/* SLIDER */

.slider {
  width: 100%;
  height: 50rem;
  margin: 0 auto;
  position: relative;

  overflow: hidden;
}

.slide {
  position: absolute;
  top: 0;
  left: 5%;
  width: 34rem;
  height: 31rem;
  padding: 2.2rem;

  display: flex;
  align-items: center;
  justify-content: center;

  transition: transform 1s;
}

.slider__btn {
  position: absolute;
  top: 40%;
  z-index: 10;

  border: none;
  background-color: var(--color-extralight-gray);

  height: 3.2rem;
  width: 3.2rem;
}

.slider__btn--left {
  left: 2%;
  transform: translate(-50%, -50%);
}

.slider__btn--right {
  right: -2%;
  transform: translate(50%, -50%);
  transform: rotate(180deg);
}

.slider__svg--active {
  fill: var(--color-primary);
  cursor: pointer;
}

.dots {
  position: absolute;
  bottom: 20%;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
}

.dots__dot {
  border: none;
  background-color: var(--color-mid-gray);
  height: 0.8rem;
  width: 0.8rem;
  border-radius: 50%;
  margin-right: 1.7rem;
  cursor: pointer;
  transition: all 0.5s;
}

.dots__dot:last-child {
  margin: 0;
}

.dots__dot--active {
  background-color: var(--color-primary);
}

.review {
  width: 100%;
  position: relative;
}

.review__author {
  display: grid;
  grid-template-columns: 6.5rem 1fr;
  column-gap: 2rem;
}

.review__photo {
  grid-row: 1 / span 2;
  width: 4.4rem;
  border-radius: 50%;
}

.review__name {
  font-size: 1.6rem;
  margin-bottom: 0.4rem;
}

.review__location {
  font-family: 'GraphikLCG-light';
  font-size: 1.2rem;
  line-height: 1.6px;
}

.review__text {
  font-family: 'GraphikLCG-light';
  font-size: 1.4rem;
  margin: 2rem 0;
}
